import { _decorator, Component, Enum, Node } from "cc";
import { UIManager } from "./UIManager";

const { ccclass, property } = _decorator;

export enum GameState {
  LOADING,
  GAME_START,
  GAME_RUNNING,
  GAME_OVER,
}

@ccclass("GameManager")
export class GameManager extends Component {

  @property({ type: Enum(GameState) })
  
  currentState: GameState = GameState.GAME_START;
  
  @property(UIManager) uIManager: UIManager ;

  protected start(): void {
    this.scheduleOnce(() => {
      this.setState(GameState.LOADING)

    },3)
  }
  
  setState(newState: GameState) {
    this.currentState = newState;
    this.handleStateChange(newState);
  }

  handleStateChange(state: GameState) {
    this.currentState = state;
    switch (state) {
      case GameState.LOADING:
        this.onLoading();
        break;
      case GameState.GAME_START:
        this.onGameStart();
        break;
      case GameState.GAME_RUNNING:
        this.onGameRunning();
        break;
      case GameState.GAME_OVER:
        this.onGameOver();
        break;
    }
  }
  onLoading() {
    console.log("Game is loading...");
    // Yükleme işlemleri burada yapılır
    this.uIManager.handleLoading()
  }

  onGameStart() {
    console.log("Game started!");
    // Oyun başlangıç işlemleri burada yapılır
  }

  onGameRunning() {
    console.log("Game is running...");
    // Oyun devam ederken yapılacak işlemler burada yapılır
  }

  onGameOver() {
    console.log("Game over.");
    // Oyun bittiğinde yapılacak işlemler burada yapılır
  }
  protected update(dt: number): void {
    if(this.currentState === GameState.LOADING){
     
    }
  }
}
